<template>
	<div class="main">
		<p align = "right">	
			<button class="click-able" v-on:click.prevent="goLoginLogout">{{login_btn}}</button> 
		</p>
		
		<h1 class="menu"> </h1>
		
		<form action="/srch/list/star/do.go">
			<input v-model="tag_nm" type="text" class="search" placeholder="  , , ,    !">
			<button class="search_submit click-able" v-on:click.prevent="starSrch">
		</form>
	
		<br>	
		<button class="click-able" v-on:click.prevent="goStarSrchDtl"> </button>
		<button class="click-able" v-on:click.prevent="goPostList"> </button>
		<button class="click-able" v-on:click.prevent="goMmList">/ </button>
		<button class="click-able" v-on:click.prevent="goRankList"> </button>
	</div>
</template>

<script>
// session 
// https://www.npmjs.com/package/vue-sessionstorage
import axios from 'axios'

export default {
  name: 'star-srch',
  data () {
    return {
      login_btn: (this.$session.exists()) ? '' : ''
    }
  },
  methods: {
    goLoginLogout: function () {
      let self = this
      if (!self.$session.exists()) {
        self.$router.push({ name: 'Login' })
      } else {
        axios.post('/logout/do.go', {})
        .then(function (response) {
          alert(JSON.stringify(response))
          self.$session.destroy()
          self.$data.login_btn = ''
          self.$router.push({ name: 'Main' })
        }).catch(function (error) {
          alert(JSON.stringify(error))
        })
      }
    },
    goStarSrchDtl: function () {
      this.$router.push({ name: 'StarSrchDtl' })
    },
    goPostList: function () {
      this.$router.push({ name: 'PostList' })
    },
    goMmList: function () {
      this.$router.push({ name: 'MmList' })
    },
    goRankList: function () {
      alert('go to ')
      // this.$router.push({ name: 'PostList' })
    },
    starSrch: function () {
      this.$router.push({ name: 'StarSrchRstList' })
      /* axios.post('/star/srch/list/star/do.go', {
        tag_nm: this.tag_nm
      })
      .then(function (response) {
        alert(JSON.stringify(response))
      })
      .catch(function (error) {
        alert(JSON.stringify(error))
      }) */
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	form{ display: inline-flex; }
</style>



