<template>
<ul class="navbar">
	<li class="left"><a class="click-able" v-on:click.prevent="goMyInfo">{{ getIntro() }}</a></li>	
	<li class="right"><a v-on:click.prevent="goRankList" class="right click-able"> </a></li> 
	<li class="right"><a v-on:click.prevent="goScheList" class="right click-able"></a></li> 
	<li class="right"><a v-on:click.prevent="goPostList" class="right click-able"> </a></li> 
	<li class="right"><a v-on:click.prevent="goStarSrchDtl"	class="right click-able" > </a></li> 
	<li class="right"><a v-on:click.prevent="goMain"class="right click-able" > </a></li> 
</ul>
</template>

<script>

export default {
  name: 'main-nav',
  data () {
    return {
      // this.$session.get('usr').mem_id: ''
      mem_id: (this.$session.exists()) ? this.$session.get('usr').mem_id : ''
    }
  },
  methods: {
    getIntro: function () {
      return this.$data.mem_id + ', !'
    },
    goMyInfo: function () {
      if (this.$session.exists()) {
        this.$router.push({ name: 'MyInfo' })
      } else {
        this.$router.push({ name: 'Login' })
      }
    },
    goMain: function () {
      this.$router.push({ name: 'Main' })
    },
    goStarSrchDtl: function () {
      this.$router.push({ name: 'StarSrchDtl' })
    },
    goPostList: function () {
      this.$router.push({ name: 'PostList' })
    },
    goScheList: function () {
      alert('go to ')
      // this.$router.push({ name: 'PostList' })
    },
    goRankList: function () {
      alert('go to ')
      // this.$router.push({ name: 'PostList' })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



